import React, { Component } from "react";
import { Dropdown } from "semantic-ui-react";

class SensorLocations extends Component {
	state = {};
	render() {
		const sensorOptions = [];
		Object.keys(this.props.sensorOptions).forEach(index => {
			sensorOptions[index] = {
				text: this.props.sensorNames[index],
				value: this.props.sensorOptions[index]
			};
		});

		return (
			<Dropdown
				onChange={this.props.handleChangeSensor}
				placeholder="Select Sensors"
				selection
				multiple
				options={sensorOptions}
			/>
		);
	}
}

export default SensorLocations;
