import React, { Component } from "react";
import logo from "./onclogo_135x135_1.png";
import axios from "axios";
import Container from "./components/Container";
import Header from "./components/Header";
import SideBar from "./components/SideBar";
import "./css/App.css";
import "./css/semantic.min.css";

class App extends Component {
	constructor(props) {
		super(props);
		this.state = {
			user: "",
			sensors: "",
			sensorOptions: [],
			sensorNames: [],
			location: "select",
			locationCodes: []
		};
	}

	componentDidMount() {
		this.getlocationCodes();
		this.getSensors();
	}

	render() {
		return (
			<div className="root">
				<div>
					<Header
						logo={logo}
						sensorNames={this.state.sensorNames}
						sensorOptions={this.state.sensorOptions}
						locationOptions={this.state.locationCodes}
						user={this.state.user}
						handleLogout={() => this.setUser("")}
						handleChangeSensor={this.handleChangeSensor}
						handleChangeLocation={this.handleChangeLocation}
					/>

					<div>
						<SideBar />
					</div>

					{this.state.location === "select" && (
						<div className="ui container">
							<div className="ui padded grid">
								Select a location from the dropdown to get
								started
							</div>
						</div>
					)}
					{this.state.location !== "select" && (
						<Container
							className="semantic"
							locationCode={this.state.location}
							sensors={this.state.sensors}
						/>
					)}
				</div>
			</div>
		);
	}

	handleChangeSensor = (event, data) => {
		console.log(data.value);
		this.setState({ sensors: data.value.join() });
	};

	handleChangeLocation = (event, data) => {
		console.log(data.value);
		this.setState({ location: data.value });
	};

	setUser = user => {
		this.setState({ user: user });
	};

	// https://data.oceannetworks.ca/api/locations?method=get&token=079f20e6-4fc3-41ab-832e-a42943f59186&deviceCategoryCode=METSTN
	// https://data.oceannetworks.ca/api/devices?method=get&token=079f20e6-4fc3-41ab-832e-a42943f59186&deviceCategoryCode=METSTN
	// https://data.oceannetworks.ca/api/deviceCategories?method=get&token=079f20e6-4fc3-41ab-832e-a42943f59186&deviceCategoryCode=METSTN
	// https://data.oceannetworks.ca/api/properties?method=get&token=079f20e6-4fc3-41ab-832e-a42943f59186&deviceCategoryCode=METSTN
	// https://data.oceannetworks.ca/api/dataProducts?method=get&token=079f20e6-4fc3-41ab-832e-a42943f59186&deviceCategoryCode=METSTN

	getSensors() {
		axios
			.get(
				"https://cors-anywhere.herokuapp.com/https://data.oceannetworks.ca/api/properties",
				{
					params: {
						method: "get",
						token: "43b478f3-8f59-41e8-a24b-fa52eb3ad01f",
						deviceCategoryCode: "METSTN"
					},
					headers: {
						"content-type": "application/x-www-form-urlencoded"
					}
				}
			)
			.then(response => {
				console.log(response);
				let sensorOptions = [];
				let sensorNames = [];

				Object.keys(response.data).forEach(index => {
					sensorOptions[index] = response.data[index].propertyCode;
					sensorNames[index] = response.data[index].propertyName;
				});

				this.setState({
					sensorOptions: sensorOptions,
					sensorNames: sensorNames
				});
			})
			.catch(error => {
				console.log(error);
			});
	}
	// https://data.oceannetworks.ca/api/locations?method=get&token=079f20e6-4fc3-41ab-832e-a42943f59186&deviceCategoryCode=METSTN

	getlocationCodes() {
		axios
			.get(
				"https://cors-anywhere.herokuapp.com/https://data.oceannetworks.ca/api/locations",
				{
					params: {
						method: "get",
						token: "43b478f3-8f59-41e8-a24b-fa52eb3ad01f",
						deviceCategoryCode: "METSTN"
					},
					headers: {
						"content-type": "application/x-www-form-urlencoded"
					}
				}
			)
			.then(response => {
				console.log(response);
				let locationCodes = [];

				Object.keys(response.data).forEach(index => {
					locationCodes[index] = response.data[index].locationCode;
				});

				this.setState({
					locationCodes: locationCodes
				});
			})
			.catch(error => {
				console.log(error);
			});
	}
}

export default App;
